"use client";

import { useEffect, useState } from "react";
import { useI18n } from "@/i18n/i18nProvider";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { BACKEND_URL } from "@/lib/config";

type InvoiceHistoryItem = {
  id: string;
  price: number; // už jen number – žádné uniony
  weight: string;
  uploadedAt: string;
};

type ShipmentSummary = {
  id: string;
  trackingNumber: string;
  companyName: string;
};

// jak to leze z backendu
type ShipmentHistoryApiResponse = {
  shipment: {
    id: string;
    trackingNumber: string;
    provider: string;
    mode: "EXPORT" | "IMPORT";
    originCountry: string;
    destinationCountry: string;
    company: {
      id: string;
      name: string;
    };
  };
  history: {
    id: string;
    price: number | string;
    weight: string;
    uploadedAt: string;
  }[];
};

type ShipmentHistoryDialogProps = {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  shipment: ShipmentSummary | null;
};

export function ShipmentHistoryDialog({
  open,
  onOpenChange,
  shipment,
}: ShipmentHistoryDialogProps) {
  const { t, locale } = useI18n();

  const [items, setItems] = useState<InvoiceHistoryItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // helper na formátování data
  const formatDateTime = (iso: string) =>
    new Date(iso).toLocaleString(locale === "cs" ? "cs-CZ" : "en-US");

  useEffect(() => {
    if (!open || !shipment) return;

    const loadHistory = async () => {
      try {
        setLoading(true);
        setError(null);

        const res = await fetch(
          `${BACKEND_URL}/api/shipments/${shipment.id}/history`
        );

        if (!res.ok) {
          throw new Error(`Failed to fetch history: ${res.status}`);
        }

        const data = (await res.json()) as ShipmentHistoryApiResponse;

        const normalized: InvoiceHistoryItem[] = data.history.map((h) => ({
          id: h.id,
          price: typeof h.price === "string" ? Number(h.price) : h.price,
          weight: h.weight,
          uploadedAt: h.uploadedAt,
        }));

        setItems(normalized);
      } catch (err) {
        console.error(err);
        setError(t("shipmentHistory.error"));
      } finally {
        setLoading(false);
      }
    };

    loadHistory();
  }, [open, shipment, t]);

  return (
    <Dialog
      open={open}
      onOpenChange={(next) => {
        if (!next) {
          // při zavření smažeme data, ať nezůstávají stará
          setItems([]);
          setError(null);
        }
        onOpenChange(next);
      }}
    >
      <DialogContent className="max-w-lg">
        <DialogHeader>
          <DialogTitle>
            {t("shipmentHistory.title")}{" "}
            {shipment
              ? `– ${shipment.trackingNumber} (${shipment.companyName})`
              : ""}
          </DialogTitle>
        </DialogHeader>

        {!shipment && (
          <div className="text-sm text-slate-500">
            {t("shipmentHistory.noShipmentSelected")}
          </div>
        )}

        {shipment && (
          <>
            {loading && (
              <div className="text-sm text-slate-500">
                {t("shipmentHistory.loading")}
              </div>
            )}

            {error && <div className="text-sm text-red-600">{error}</div>}

            {!loading && !error && items.length === 0 && (
              <div className="text-sm text-slate-500">
                {t("shipmentHistory.empty")}
              </div>
            )}

            {!loading && !error && items.length > 0 && (
              <div className="max-h-72 overflow-y-auto">
                <Table className="text-sm">
                  <TableHeader>
                    <TableRow>
                      <TableHead>{t("shipmentHistory.colId")}</TableHead>
                      <TableHead className="text-right">
                        {t("shipmentHistory.colPrice")}
                      </TableHead>
                      <TableHead className="text-right">
                        {t("shipmentHistory.colWeight")}
                      </TableHead>
                      <TableHead>
                        {t("shipmentHistory.colUploadedAt")}
                      </TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {items.map((h) => (
                      <TableRow key={h.id}>
                        <TableCell className="font-mono text-xs">
                          {h.id}
                        </TableCell>
                        <TableCell className="text-right">
                          {h.price.toFixed(2)}
                        </TableCell>
                        <TableCell className="text-right">{h.weight}</TableCell>
                        <TableCell>{formatDateTime(h.uploadedAt)}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </>
        )}
      </DialogContent>
    </Dialog>
  );
}
