"use client";

import { useI18n } from "@/i18n/i18nProvider";

type ShipmentCardProps = {
  trackingNumber: string;
  companyName: string;
  provider: string;
  price: number;
  currency?: string;
  createdAt: string;
  originCountry: string;
  destinationCountry: string;
  mode: string;
  hasHistory: boolean;
  onShowHistory: () => void;
};

export function ShipmentCard({
  trackingNumber,
  companyName,
  provider,
  price,
  currency = "CZK",
  createdAt,
  originCountry,
  destinationCountry,
  mode,
  hasHistory,
  onShowHistory,
}: ShipmentCardProps) {
  const { locale, t } = useI18n();

  const formattedPrice = new Intl.NumberFormat(
    locale === "cs" ? "cs-CZ" : "en-US",
    {
      style: "currency",
      currency,
    }
  ).format(price);

  const formattedDate = new Date(createdAt).toLocaleDateString(
    locale === "cs" ? "cs-CZ" : "en-US"
  );

  return (
    <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm flex flex-col gap-2">
      <div className="flex items-center justify-between">
        <div className="text-xs text-slate-500">{trackingNumber}</div>
        <div className="text-xs text-slate-500">{provider}</div>
      </div>

      <div className="text-sm font-medium text-slate-900">{companyName}</div>

      <div className="text-lg font-semibold">{formattedPrice}</div>

      <div className="text-xs text-slate-500">
        {originCountry} → {destinationCountry} · {mode}
      </div>

      <div className="text-xs text-slate-400">{formattedDate}</div>

      {hasHistory && (
        <button
          type="button"
          onClick={onShowHistory}
          className="mt-2 self-start text-xs text-blue-600 hover:underline"
        >
          {t("dashboard.showHistory")}
        </button>
      )}
    </div>
  );
}
